import axios from "axios"
import authHeader from '../../services/auth-header'

const prodURL = process.env.VUE_APP_prodURL

export const tasks = {
    state: {
        tasks: []
    },

    getters: {
        allTasks: (state) => state.tasks
    },
    
    actions: {
        async fetchTasks({ commit }) {
            const response = await axios.get(prodURL+'/tasks', {headers: authHeader()})
            
            commit('setTasks', response.data)
        },

        async addTask({ commit }, description) {
            const response = await axios.post(prodURL+'/tasks', { description, completed: false}, {headers: authHeader()})

            commit('newTask', response.data)
        },

        async filterTasks({ commit }, e) {
            let limit = e.target.options[e.target.options.selectedIndex].value
            if( limit === 'true' || limit === 'false' || limit === 'default')
                limit = document.getElementById('select-limit').options[document.getElementById('select-limit').options.selectedIndex].value

            let completed = e.target.options[e.target.options.selectedIndex].value==='true'?'pass':'nonpass'
            let index = document.getElementById('select-completeness').options.selectedIndex

            if(completed === 'nonpass' && index === 2){
                completed = completed !== 'nonpass'
            }else if(completed === 'nonpass' && index === 0) {
                completed = ''
            }else{
                completed = completed==='pass'
            }

            const response = await axios.get(prodURL+`/tasks?limit=${limit}&completed=${completed}`, {headers: authHeader()})

            commit('setTasks', response.data)
        },

        async deleteTask({ commit }, id) {
            await axios.delete(prodURL+`/tasks/${id}`, {headers: authHeader()})

            commit('removeTask', id)
        },

        async updateTask({ commit }, updTask) {
            const response = await axios.patch(prodURL+`/tasks/${updTask._id}`, updTask, {headers: authHeader()})

            commit('updateTask', response.data)
        }
    },

    mutations: {
        setTasks: (state, tasks) => (state.tasks = tasks),
        newTask: (state, task) => state.tasks.unshift(task),
        removeTask: (state, id) => {
            state.tasks = state.tasks.filter((task) => {
                return task._id !== id
            })
        },
        updateTask: (state, updTask) => {
            const index = state.tasks.findIndex(task => task._id === updTask._id)
            if(index !== -1) {
                state.tasks.splice(index, 1, updTask)
            }
        }
    }
}