<template>
  <div class="col-md-12">
      <div class="card card-container">
          <img src="//ssl.gstatic.com/accounts/ui/avatar_2x.png" class="profile-img-card" id="profile-img">
          <ValidationObserver v-slot="{ invalid }">
            <form name="form" @submit.prevent="handleLogin">
            <div class="form-group">
                <label for="email">Email</label>
                <ValidationProvider name="email" rules="email|required" v-slot="{ errors }">
                    <input v-model="user.email" type="text" class="form-control"/>
                    <div class="alert-danger" >
                        {{ errors[0] }}
                    </div>
                </ValidationProvider>
            <div class="form-group">
                <label for="password">Password</label>
                <ValidationProvider name="password" rules="required" v-slot="{ errors }">
                    <input v-model="user.password" type="password" class="form-control"/>
                    <div class="alert-danger" role="alert">
                        {{ errors[0] }}
                    </div>
                </ValidationProvider>
            </div>
            <div class="form-group" >
                <button type="submit" class="btn btn-primary btn-block" :disabled="invalid">
                    <span v-show="loading" class="spinner-border spinner-border-sm"></span>
                    <span>Login</span>
                </button>
            </div>
            <div class="form-group">
                <div v-if="message" class="alert alert-danger" role="alert">{{message}}</div>
            </div>    
            </div>
            </form>    
          </ValidationObserver>
      </div>
  </div>
</template>

<script>
import { ValidationObserver, ValidationProvider, extend } from 'vee-validate'
import { required, email } from 'vee-validate/dist/rules'

extend('required', {
    ...required,
    message: 'This field is required'
}) 

extend('email', email)

export default {
    name: "Login",
    components: {
        ValidationObserver,
        ValidationProvider
    },
    data() {
        return {
            user: {
                email: '',
                password:''
            },
            loading: false,
            message: ''
        }
    },
    computed: {
        loggedIn() {
            return this.$store.state.auth.isLoggedIn
        }
    },
    created() {
        if (this.loggedIn) {
            this.$router.push('/me')
        }
    },
    methods: {
        handleLogin() {
            if (this.user.email && this.user.password){
                this.$store.dispatch('auth/login', this.user).then(
                    () => {    
                        this.$router.push('/me') 
                    },
                    error => {
                        this.loading = false
                        this.message = (error.response && error.response.data) || error.message ||
                        error.toString()
                    }
                )
            }
        }
    }
}
</script>

<style scoped>
    label {
        display: block;
        margin-top: 10px;
    }

    .card-container.card {
        max-width: 350px !important;
        padding: 40px 40px
    }

    .card {
        background-color: #f7f7f7;
        padding: 20px 25px 30px;
        margin: 0 auto 25px;
        margin-top: 50px;
        -moz-border-radius: 2px;
        -webkit-border-radius: 2px;
        border-radius: 2px;
        -moz-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
        -webkit-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
        box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
    }

    .profile-img-card {
        width: 96px;
        height: 96px;
        margin: 0 auto 10px;
        display: block;
        -moz-border-radius: 50%;
        -webkit-border-radius: 50%;
        border-radius: 50%
    }
</style>