<template>
  <div>
      Filter Tasks by:
      <select id="select-limit" @change="filterTasks($event)">
          <option selected="selected">number</option>
          <option id="default-limit" value="50">50</option>
          <option value="20">20</option>
          <option value="10">10</option>
          <option value="5">5</option>
      </select>
      <select id="select-completeness" @change="filterTasks($event)">
          <option selected="selected" value="default">completeness</option>
          <option id="complete-task" value="true" >completed</option>
          <option id="incomplete-task" value="false">incomplete</option>
      </select>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
export default {
    name: "FilterTasks",
    methods: {
        ...mapActions(["filterTasks"]),
    },
    data() {
        return {
            sort: {
                limit: 50,
                completed: false
            }
        }
    }
}
</script>

<style scoped>
    select {
        margin-top: 20px;
        padding: 6px;
        border: #41b883 1px solid;
    }
</style>
