<template>
  <div id="app">
      <nav class="navbar navbar-expand navbar-dark bg-dark">
        <a href class="navbar-brand" @click.prevent>Task Manager</a>
        <div class="navbar-nav mr-auto">
          <li class="nav-item">
            <router-link to="/" class="nav-link">Home</router-link>
          </li>
          <li>
            <router-link to="/about" class="nav-link">About</router-link>
          </li>
          <li class="nav-item">
            <router-link to="/tasks" @click="toTaskView" class="nav-link">Task-Manager</router-link>
          </li>
          <li>
            <router-link to="/login" class="nav-link">User</router-link>
          </li>
        </div>
        <div v-if="currentUser" class="navbar-nav ml-auto">
          <li class="nav-item">
            <router-link to="/me" class="nav-link">
              {{ currentUser.name }}
            </router-link>
          </li>
          <li class="nav-item">
            <a href @click.prevent="logOut" class="nav-link">
              <font-awesome-icon icon="sign-out-alt"/>Log out</a>
          </li>
        </div>
        <div v-if="!currentUser" class="navbar-nav ml-auto">
          <li class="nav-item">
            <router-link to="/register" class="nav-link">
              <font-awesome-icon icon="user-plus"/>Sign Up
            </router-link>
          </li>
          <li class="nav-item">
            <router-link to="/login" class="nav-link">
              <font-awesome-icon icon="sign-in-alt"/>Login</router-link>
          </li>
        </div>
      </nav>
      <div class="container">
        <router-view />
      </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loggedIn: false
    }
  },  
  computed: {
    currentUser(){
      return this.$store.state.auth.user
    }
  },
  methods: {
    toTaskView() {
      this.loggedIn = this.$store.state.auth.isLoggedIn
      if(this.loggedIn)
        this.$router.push('/tasks')
    },
    logOut() {
      this.$store.dispatch('auth/logout')
      this.$router.push('/login')
    }
  }
};
</script>
