<template>
  <div class="col-md-12">
    <div class="card card-container">
      <img
        id="profile-img"
        src="//ssl.gstatic.com/accounts/ui/avatar_2x.png"
        class="profile-img-card"
      />
      <ValidationObserver v-slot="{ invalid }">
        <form name="form" @submit.prevent="handleRegister">
          <div v-if="!successful">
              <label for="name">Name</label>
              <ValidationProvider name="name" rules="required|minmax:3,20" v-slot="{ errors }">
                <input
                  v-model="user.name"
                  type="text"
                  class="form-control"
                />
                <div class="alert-danger">{{ errors[0] }}</div>
              </ValidationProvider>
              <label for="age">Age</label>
              <ValidationProvider name="age" rules="between:1,122" v-slot="{ errors }">
                <input
                  v-model.number="user.age"
                  type="text"
                  class="form-control"
                />
                <div class="alert-danger">{{ errors[0] }}</div>
              </ValidationProvider>
              <label for="email">Email</label>
              <ValidationProvider name="email" rules="required|email|max:50" v-slot="{ errors }">
                <input
                  v-model="user.email"
                  type="email"
                  class="form-control"
                />
                <div class="alert-danger">{{ errors[0] }}</div>
              </ValidationProvider>
              <label for="password">Password</label>
              <ValidationProvider name="password" rules="required|minmax:6,40" v-slot="{ errors }">
              <input
                v-model="user.password"
                type="password"
                class="form-control"
              />
              <div class="alert-danger">{{ errors[0] }}</div>
              </ValidationProvider><br>
              <button type="submit" :disabled="invalid" class="btn btn-primary btn-block">Sign Up</button>
          </div>
        </form>
      </ValidationObserver>
      <div
        v-if="message"
        class="alert"
        :class="successful ? 'alert-success' : 'alert-danger'"
      >{{message}}</div>
    </div>
  </div>
</template>

<script>
import { ValidationProvider, ValidationObserver, extend } from 'vee-validate'
import { required, email, between } from 'vee-validate/dist/rules'

// No message specified
extend('email', email)

// Override the default message.
extend('required', {
  ...required,
  message: 'This field is required'
})

extend('between', {
  ...between,
  message: 'Age is not Valid'
})

extend('max', {
  validate(value, args) {
    return value.length <= args
  }
})

extend('minmax', {
  validate(value, { min, max }) {
    return value.length >= min && value.length <= max
  },
  params: ['min', 'max']
})

export default {
  name: 'Register',
  components: {
    ValidationProvider,
    ValidationObserver
  }, 
  data() {
    return {
      user: {
        name: '',
        password: '',
        email: '',
        age: ''
      },
      successful: false,
      message: ''
    };
  },
  computed: {
    loggedIn() {
      return this.$store.state.auth.isLoggedIn;
    }
  },
  mounted() {
    if (this.loggedIn) {
      this.$router.push('/me');
    }
  },
  methods: {
    handleRegister() {
      this.message = '';
      this.submitted = true;
        this.$store.dispatch('auth/register', this.user).then(
          () => {   
            this.successful = true;
            alert('register success, please login')
            this.$router.push('/login')
          },
          error => {
            this.message =
              (error.response && error.response.data) ||
              error.message ||
              error.toString();
            this.successful = false;
          }
        );
    }
  }
};
</script>

<style scoped>
label {
  display: block;
  margin-top: 10px;
}

.card-container.card {
  max-width: 350px !important;
  padding: 40px 40px;
}

.card {
  background-color: #f7f7f7;
  padding: 20px 25px 30px;
  margin: 0 auto 25px;
  margin-top: 50px;
  -moz-border-radius: 2px;
  -webkit-border-radius: 2px;
  border-radius: 2px;
  -moz-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
}

.profile-img-card {
  width: 96px;
  height: 96px;
  margin: 0 auto 10px;
  display: block;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
}
</style>